import { isPresent } from '../utils';
import { SortDescriptor } from '../sort-descriptor';
import { State } from './state';

/**
 * @hidden
 */
const serializeSort = (orderby: Array<SortDescriptor>): string => {
    const str = orderby
        .filter(sort => isPresent(sort.dir))
        .map(sort => {
            const order = sort.field.replace(/\./g, "/");
            return sort.dir === "desc" ? order + " desc" : order;
        }).join(",");

    return str ? `$orderby=${str}` : str;
};

/**
 * @hidden
 */
const rules = (key, state) => ({
    "skip": `$skip=${state.skip}`,
    "take": `$top=${state.take}`,
    "sort": serializeSort(state.sort || [])
}[key]);

/**
 * Converts the data state of the Grid to an OData string.
 */
export const toODataString = (state: State): string => Object.keys(state).map(x => rules(x, state)).join('&');



